@import url(https://fonts.googleapis.com/css?family=Red+Hat+Mono:wght@100..900&display=swap);
.gs-grid{--cellSize: min(2.2em, 6.25vw);--brColor: rgba(37, 38, 40, 0.5);display:grid;grid-gap:1em;gap:1em}.gs-grid__field{--columns: 16;--rows: 7;border:1px solid var(--brColor);border-width:0 0 1px 1px;display:grid;grid-auto-flow:column;grid-template-columns:repeat(var(--columns), 1fr);grid-template-rows:repeat(var(--rows), 1fr)}.gs-grid__cursor{background-color:rgba(140,88,29,.25);grid-column:calc(var(--cursor) + 1);grid-row:1/span var(--rows);width:var(--cellSize);pointer-events:none}.gs-key{--face: #3c3f44;--button-gradient: radial-gradient(#8881, #0000);background-color:var(--face);background-image:var(--button-gradient);border:1px solid var(--brColor);border-radius:2px;border-width:1px 1px 0 0;box-sizing:border-box;display:grid;grid-column:calc(var(--step) + 1);grid-row:calc(var(--rows) - var(--note));grid-template-areas:"main";height:var(--cellSize);place-items:center;transition:background-color 100ms,color 100ms;-webkit-user-select:none;-ms-user-select:none;user-select:none;width:var(--cellSize)}.gs-key:before{content:"";grid-area:main;padding-top:100%}.gs-key>*{grid-area:main}.gs-key--is-active{--face: #d4902b;--gd-start: #fff566;--gd-end: #f000;--button-gradient: radial-gradient(var(--gd-start), var(--gd-end));box-shadow:0 0 16px 4px rgba(255,214,51,.1);color:#252628;transition:background-color 800ms,color 100ms;z-index:1}.gs-key__label{display:none;font-family:"Red Hat Mono";font-size:.625em;font-weight:100}
.splainer{display:grid;font-size:.75em;grid-gap:1em;gap:1em;grid-auto-flow:column;padding:1em 0}
.eg-synthoptions{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:2em;gap:2em;justify-self:flex-end}.eg-synth__option{align-items:center;display:grid;grid-gap:1em;gap:1em;grid-auto-flow:column;position:relative;-webkit-user-select:none;-ms-user-select:none;user-select:none}.eg-synth__led{display:none}.eg-synth__led+label{--size: 1em;display:inline-grid;grid-template-areas:"led label";grid-template-columns:var(--size);grid-column-gap:.5em;-webkit-column-gap:.5em;column-gap:.5em}.eg-synth__led+label:before,.eg-synth__led+label:after{border-radius:var(--size);border:1px solid #3c3f44;content:"";grid-area:led;height:var(--size);place-self:center;width:var(--size)}.eg-synth__led+label:before{background-image:radial-gradient(#fff1 50%, #fbff0033)}.eg-synth__led+label:after{--size: 0.9em}.eg-synth__led:checked+label:after{background-image:radial-gradient(circle at 50% 40%, #fff566, #8c581d);box-shadow:0 0 .5em .1em #8c581d}.eg-synth__switch{display:none}.eg-synth__switch+label{--len: 1.7em;--flip-len: calc(var(--len) / 3);align-items:center;display:inline-grid;grid-gap:.5em;gap:.5em;grid-template-areas:"check label";grid-template-columns:var(--len) auto}.eg-synth__switch+label:before{align-items:center;background-color:#3c3f44;border-radius:1em;content:"";grid-area:check;height:1em;line-height:0;width:var(--len)}.eg-synth__switch+label:after{--color: #9198a1;background-color:var(--color);background-blend-mode:overlay;background-image:radial-gradient(circle at bottom, #fff9, #0008),radial-gradient(#0000 60%, #fff);border:1px solid #252628;border-radius:1em;content:"";grid-area:check;height:1em;justify-self:center;transform:translate(calc(-1 * var(--flip-len)), 0);transition:transform .08s ease-in-out;width:1em}.eg-synth__switch:checked+label:after{--color: #d4902b;transform:translate(var(--flip-len), 0)}
html,body{font-family:"Red Hat Mono";margin:0;padding:0}html{background-color:#252628;color:#9198a1;height:100vh}body{display:grid;place-items:center;height:100%}a,a:visited{color:#d4902b}button{background-color:#8c581d;border-radius:4px;border:none;color:#fff;outline:none;padding:.5em 1em}.eg-app{display:grid;place-items:center;grid-gap:1em;gap:1em}
